export default function EnhancedTable() {
  const classes = useStyles();
  const [rowData, setRowData] = React.useState([]);
  const [selectedDateFr, setSelectedDateFr] = React.useState(new Date());
  const [selectedDateTo, setSelectedDateTo] = React.useState(new Date());
  const [order, setOrder] = React.useState("asc");
  const [orderBy, setOrderBy] = React.useState("calories");
  const [selected, setSelected] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [dense, setDense] = React.useState(false);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const handleRequestSort = (event, property) => {
    const isDesc = orderBy === property && order === "desc";
    setOrder(isDesc ? "asc" : "desc");
    setOrderBy(property);
  };

  const handleSelectAllClick = event => {
    if (event.target.checked) {
      const newSelecteds = rows.map(n => n.name);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1)
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    console.log("curPage: " + newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    console.log("handleChangeRowsPerPage");
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleChangeDense = event => {
    setDense(event.target.checked);
  };

  const handleDateChangeFr = date => {
    setSelectedDateFr(date);
  };

  const handleDateChangeTo = date => {
    setSelectedDateTo(date);
  };

  const converDateFormat = yyyymmdd => {
    const MMM = {
      "01": "Jan",
      "02": "Feb",
      "03": "Mar",
      "04": "Apr",
      "05": "May",
      "06": "Jun",
      "07": "Jul",
      "08": "Aug",
      "09": "Sep",
      "10": "Oct",
      "11": "Nov",
      "12": "Dec"
    };

    const yyyy = yyyymmdd.slice(0, 4);
    const mm = yyyymmdd.slice(4, 6);
    const dd = yyyymmdd.slice(-2);

    return `${MMM[mm]} ${dd}, ${yyyy}`;
  };

  const getData = () => {
    fetch(
      "/order/statistic/packing_status?proc_date_fr=20190101&proc_date_to=20191231"
    )
      .then(results => {
        console.log(results);
        return results.json();
      })
      .then(data => {console.log(data);
        setRowData(data);});
    // const data = rows;
    // setRowData(data);
  };

  const isSelected = name => selected.indexOf(name) !== -1;

  const emptyRows =
    rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <EnhancedTableToolbar numSelected={selected.length} />
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <Grid container justify="space-around">
            <KeyboardDatePicker
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              id="date-picker-fr"
              label="Date picker inline"
              value={selectedDateFr}
              onChange={handleDateChangeFr}
              KeyboardButtonProps={{
                "aria-label": "change date"
              }}
            />
            <KeyboardDatePicker
              disableToolbar
              variant="inline"
              format="MM/dd/yyyy"
              margin="normal"
              id="date-picker-to"
              label="Date picker inline"
              value={selectedDateTo}
              onChange={handleDateChangeTo}
              KeyboardButtonProps={{
                "aria-label": "change date"
              }}
            />
            <Button variant="contained" color="primary" onClick={getData}>
              Search
            </Button>
          </Grid>
        </MuiPickersUtilsProvider>
        <div className={classes.tableWrapper}>
          <Table
            className={classes.table}
            aria-labelledby="tableTitle"
            size={dense ? "small" : "medium"}
            aria-label="enhanced table"
          >
            <EnhancedTableHead
              classes={classes}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rowData.length}
            />
            <TableBody>
              {stableSort(rowData, getSorting(order, orderBy))
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, index) => {
                  // const isItemSelected = isSelected(row.name);
                  // const labelId = `enhanced-table-checkbox-${index}`;

                  return (
                    <TableRow
                      hover
                      // onClick={event => handleClick(event, row.name)}
                      // role="checkbox"
                      // aria-checked={isItemSelected}
                      tabIndex={-1}
                      key={index}
                      // selected={isItemSelected}
                    >
                      {/* <TableCell padding="checkbox">
                        <Checkbox
                          checked={isItemSelected}
                          inputProps={{ "aria-labelledby": labelId }}
                        />
                      </TableCell> */}
                      <TableCell align="center">
                        {converDateFormat(row.PROC_DT)}
                      </TableCell>
                      <TableCell align="center">
                        {row.CHANNEL_ORDER_NO}
                      </TableCell>
                      <TableCell align="left">{row.STD_SKU}</TableCell>
                      <TableCell align="left">{row.PRODUCT_NAME}</TableCell>
                      <TableCell align="center">{row.MARKET_NAME}</TableCell>
                      <TableCell align="center">{row.EMPLOYEE_NAME}</TableCell>
                      <TableCell align="center">
                        <DoneOutlineRoundedIcon
                          color={
                            row.NO_ITEM_YN === "Y" ? "primary" : "disabled"
                          }
                        />
                      </TableCell>
                      <TableCell align="center">
                        <DoneOutlineRoundedIcon
                          color={
                            row.TAG_CHANGE_YN === "Y" ? "primary" : "disabled"
                          }
                        />
                      </TableCell>
                    </TableRow>
                  );
                })}
              {emptyRows > 0 && (
                <TableRow style={{ height: (dense ? 33 : 53) * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
      <FormControlLabel
        control={<Switch checked={dense} onChange={handleChangeDense} />}
        label="Dense padding"
      />
    </div>
  );
}
